<template>
<div class="card">
    <div class="card-header">
        <div style='display:flex;flex-direction:row'>
            <strong>{{ header_text }}</strong>
        </div>
    </div>
    <div class="card-body card-block" style='padding:0 0 0 0'>
        <div v-if="step==0">
            <div>
                <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b_view'  @click="view_record">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b_view_first'></div>
                                    <div class='b_view_first_char'>V</div>
                                </div>
                                <div class='hex_subtext'>iew Reports</div>
                                <div class='hex_legen'>All IRG Reports</div>
                            </div>                         
                        </div>
                    </div> 
                </div>             
            </div>
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b2'  @click="select_cat(2)">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b2_first'></div>
                                    <div class='b2_first_char'>Q</div>
                                </div>
                                <div class='hex_subtext'>uotation</div>
                                <div class='hex_legen'>Less than $50,000</div>
                            </div>                         
                        </div>
                    </div> 
                </div>             
            </div>
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b3' @click="select_cat(3)">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b3_first'></div>
                                    <div class='b3_first_char'>W</div>
                                </div>
                                <div class='hex_subtext'>ritten Quotation</div>
                                <div class='hex_legen'>$50,000 to $200,000</div>
                            </div>                         
                        </div>
                    </div> 
                </div>             
            </div>
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b4'  @click="select_cat(4)">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b4_first'></div>
                                    <div class='b4_first_char'>T</div>
                                </div>
                                <div class='hex_subtext'>ender</div>
                                <div class='hex_legen'>Over $200,000</div>
                            </div>                         
                        </div>
                    </div> 
                </div>  
            </div>
            <!--
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b_ssb'  @click="select_cat(5)">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b_ssb_first'></div>
                                    <div class='b_ssb_first_char'>T</div>
                                </div>
                                <div class='hex_subtext'>ender SSB</div>
                                <div class='hex_legen'>Over $200,000</div>
                            </div>                         
                        </div>
                    </div> 
                </div>  
            </div>
            -->
        </div>
            <!--
            <div class="col-12" style='text-align:center;padding-top:0'>
                <br/>
                <h1 style='font-size:3em'><u><b>Procurement Report</b></u></h1><br/><br/>
                <div class='proc_entry_card' @click="view_record">
                    View Records<br/>
                    All Generated Records
                </div><br/><br/>
                <div class='proc_entry_card' @click="select_cat(2)">
                    Less than $50,000<br/>
                    By Quotation
                </div><br/><br/>
                <div class='proc_entry_card' @click="select_cat(3)">
                    $50,001 ~ $200,000<br/>
                    By Written Quotation
                </div><br/><br/>
                <div class='proc_entry_card' @click="select_cat(4)">
                    Over $200,000<br/>
                    By Tender
                </div><br/><br/>
                <div class='proc_entry_card' @click="select_cat(5)">
                    Over $200,000 (SSB)<br/>
                    By Tender
                </div><br/><br/>
            </div>
            -->
        </div>
        <div v-if="(step>0)&&(view==false)">
            <div class="col-12">
                <div style="display:flex;flex-direction:row;margin-top:1.5%">
                    <div class="col-1"></div>
                    <div class="col-3">
                        <label v-if="cat_lv==2">Department/Committee</label>
                        <select v-if="cat_lv==2" class="form-control select" v-model="selected_dept" @change="change_dept">
                            <option v-for="items in dept_list.length" :value="dept_list[items-1].dept_id">{{ dept_list[items-1].full_dept }}</option>
                        </select>
                    </div>
                    <div class="col-1">
                    </div>
                    <div class="col-3">
                        <label>Start Date</label>
                        <input type="date" v-model="st" class="form-control" @change="new_data"/>
                    </div>
                    <div class="col-3">
                        <label>End Date</label>
                        <input type="date" v-model="ed" class="form-control" @change="new_data"/>
                    </div>
                    <div class="col-1"></div>
                </div><br/>
                <div class="col-12" style="display:flex;flex-direction:row;margin-top:0.5%">
                    <div class="col-1"></div>
                    <div class="col-2" style='padding-left:0'>
                        <label><b><u>Preview Report</u></b></label>
                    </div>
                    <div  style='margin-left:auto'>
                        <button class="form-control" @click="gen_report">Generate Report</button>
                    </div>
                    <div class="col-1"></div>
                </div>
                <div class="col-12" style="display:flex;flex-direction:row;margin-top:0.5%">
                    <div class="col-1"/>
                        <iframe :src="url" style='height:60vh;' class="fullframe col-10"/>
                    <div class="col-1"/>
                </div>               
            </div>
        </div>
        <input type='hidden' id='del_irg' @click="del_irg"/>
        <div v-if="(step>0)&&(view==true)">
            <div class="col-12" style='padding-left:0;padding-right:0'>
                <div class='col-12' style='display:flex;flex-direction:row;margin-top:1em;margin-bottom:1em'>
                    <div class='col-8'></div>
                    <div class="col-2">
                        <label>Start Date</label>
                        <input type='date' class='form-control' v-model="st" @change="search_record"/>
                    </div>
                    <div class="col-2">
                        <label>End Date</label>
                        <input type='date' class='form-control' v-model="ed" @change="search_record"/>
                    </div>
                    
                      <!--
                     <div class="col-2">                        
                        <label>Status</label>
                        <select v-model="status" class="form-control select">
                            <option value='*'>All</option>
                            <option>Waiting for head approval</option>
                            <option>Waiting for principal approval</option>
                            <option>Approved</option>
                        </select>                       
                    </div>
                    -->
                </div>
                <div class='col-12' v-html="print_list" style='padding-left:0;padding-right:0'>
                {{ print_list }}
                </div>
            </div>
    </div><br/>
    <div class="card-footer">
        <button type="submit" class="btn btn-primary btn-sm" v-if="step>1">
            <i class="fa fa-paper-plane"></i> Submit
        </button>
        <button type="button" class="btn btn-danger btn-sm" @click="previous_step">
            <i class="fa fa-undo"></i> Return
        </button>
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            dept_list:[],
            selected_dept:[],
            key:'',
            status:'*',
            print_list:'',
            view:false,
            url:'',
            base_url:'src/Procurement/report/backend/IRG.php',
            category:'',
            st:'',
            ed:'',
            step:0,
            cat_lv:0,
            header_text:'Select Report Category',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                var user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<2){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    var tdy = new Date();
    await this.fetch_dept_list();
    var month = tdy.getMonth();
    month = month+1;
    if(month>7){
        this.st = tdy.getFullYear()+'-08-01';
        this.ed = (tdy.getFullYear()+1)+'-07-31';
    } else {
        this.st = (tdy.getFullYear()-1)+'-08-01';
        this.ed = tdy.getFullYear()+'-07-31';
    }
    
},
methods:{
    async fetch_dept_list(){
        await axios.get('Procurement/report/backend/IRG_dept.php') 
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.dept_list = response.data;
                        this.selected_dept = this.dept_list[0].dept_id;
                    }
                }
            )
    },
    change_dept(){
        this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed+'&dept='+this.selected_dept;
    },
    async select_cat(cat){
        if(cat==2){
            this.header_text = 'Purchase by Quotation (>$5,000) Summary Report';
            this.cat_lv=2;
            this.step=2;
            this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed+'&dept='+this.selected_dept;
            this.$forceUpdate();          
        } else if(cat==3){
            this.header_text = 'Written Quotation Summary Report';
            this.cat_lv=3;
            this.step=3;
            this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed;
            this.$forceUpdate();
        } else if(cat==4){
            this.header_text = 'Tender Summary Report';
            this.cat_lv=4;
            this.step=4;
            this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed;
            this.$forceUpdate();
        } else if(cat==5){
            this.header_text = 'Tender Summary Report (SSB)';
            this.cat_lv=4;
            this.step=4;
            this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed+'&ssb=true';
            this.$forceUpdate();
        }
    },
    previous_step(){
        if(!(this.step==0)){
            this.view=false;
            this.header_text = 'Select Report Category';
            this.step = 0;
        } else {
            this.$router.go(-1);
        };
     },
     go_url(){
         window.location.href=this.url;
     },
     new_data(){
        this.url = this.base_url+'?cat='+this.cat_lv+'&st='+this.st+'&ed='+this.ed+'&dept='+this.selected_dept;
     },
     gen_report(){
         var formData = new FormData();
         formData.append('st',this.st);
         formData.append('ed',this.ed);
         formData.append('cat',this.cat_lv);
         var dept = '';
         if(this.cat_lv==2){
             dept = this.selected_dept;
         }
         formData.append('dept',dept);
         axios.post('Procurement/report/backend/gen_rep.php',formData)
            .then(
                (response)=>{
                    this.view=true;
                    this.header_text = "View Generated Reports";
                    axios.get('Procurement/report/backend/print_irg.php')
                    .then(
                            (response)=>{
                                this.print_list = response.data;
                            }
                        )
                }
            )
     },
     search_record(){
        var formData = new FormData();
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/report/backend/print_irg_live.php',formData)
            .then(
                    (response)=>{
                        this.print_list = response.data;
                    }
                )
     },
     view_record(){
         this.step = this.step+1;
         this.view = true;
         this.header_text = "View Generated Reports";
         axios.get('Procurement/report/backend/print_irg.php')
            .then(
                    (response)=>{
                        this.print_list = response.data;
                    }
                )
     },
     del_irg(){
         this.$confirm("Confirm Deleteing Selected Report ? ").then(
             ()=>{
                var rid = document.getElementById('tmp_id').value;
                var formData = new FormData();
                formData.append('id',rid);
                axios.post('Procurement/report/backend/del_irg.php',formData)
                    .then(
                        (response)=>{
                            this.print_list = response.data;
                        }
                    )
             }
         )      
     }
}
}
</script>



